*,
::after,
::before {
  box-sizing: border-box; }

html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

body,
html {
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  text-align: center;
  font-size: 18px;
  line-height: 20px;
  color: #fff; }
  body #root,
  html #root {
    width: 100%;
    min-height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #07bbee;
    background: linear-gradient(to bottom, #07bbee, #05dcc5); }

.btn {
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
  transition: 100ms all ease;
  box-sizing: border-box;
  outline: none;
  background: transparent;
  color: #fff;
  border: none;
  font-size: 18px; }
  .btn:hover {
    color: #aaaaaa; }
  .btn:disabled, .btn:hover:disabled {
    color: #586369; }

/* components */
.container {
  width: 920px;
  height: auto;
  margin: auto;
  padding: 1px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #333;
  box-shadow: 0 0 2rem rgba(51, 51, 51, 0.5); }
  .container video {
    width: 100%;
    height: auto;
    border: none; }
    .container video::-webkit-media-controls, .container video::-webkit-media-controls-enclosure {
      display: none !important; }
  .container #controls {
    display: flex;
    flex-direction: column;
    background-color: #333;
    width: 100%;
    /*    &.fullscreen {
      position: absolute;
      z-index: 99999999999;
      bottom: 0;
    }*/ }
    .container #controls .buttons {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 5px 10px; }
      .container #controls .buttons .left,
      .container #controls .buttons .right {
        display: flex;
        flex-direction: row; }
      .container #controls .buttons .left > * {
        margin-right: 10px; }
      .container #controls .buttons .right > * {
        margin-left: 10px; }

:-webkit-full-screen-ancestor#controls,
:-webkit-full-screen#controls,
:-moz-full-screen#controls,
:-ms-fullscreen#controls,
:fullscreen#controls {
  position: absolute;
  z-index: 99999999999;
  bottom: 0; }

/* Medium and Small devices (phones & tablets) */
@media (max-width: 992px) {
  .container {
    width: 100%; } }

progress {
  color: #ff0000;
  width: 100%;
  height: 5px;
  border: none;
  background: #aaaaaa;
  margin: 0;
  border-radius: 9px;
  vertical-align: middle;
  cursor: pointer; }
  progress[value]::-webkit-progress-bar {
    background-color: #aaaaaa;
    border-radius: 2px;
    box-shadow: 0 2px 5px rgba(51, 51, 51, 0.25) inset; }
  progress[value]::-webkit-progress-value {
    background-color: #ff0000;
    transition: 1000ms all ease; }

.time-bar {
  font-size: 14px; }



